<template>
<div>
    <div class="bkground">
        <section class="head">
            <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
            <h1>Become VIP</h1>
            <div class="chartWrap">
                <span class="iconfont cart" @click="confirm" > &#xe65a; </span></div>
        </section>
    </div>
    <div class="center">
      <img src="../common/images/shopeone.png">
        <div class="describe">
            <p>1. What is "Shopeone VIP"</p>
            <span>
                "Shopeone VIP" are special customers who can buy items with
                 "VIP Price" which is much cheaper than ordinary users. What's more, VIP can earn
                 commission by sharing your own "Invitation Code".
            </span>
            <p>2. How to become a "Shopeone VIP"</p>
            <span>
                One way is that all the last 30 days of your or
                ders' grand total reaches 4999 €. Then you'll
                becom a "Shopeone VIP" automatically in the next 30 days. You don't need to submit anything.
            </span>
             <span class="become">
                  The other way is that if you have a physical store, you can click
                  <i @click="become()" style="text-decoration: underline;">Become Cooperative VIP</i>
                  . After you finishing profile submitting, we'll cantact you as soon as possible.
            </span>
        </div>
    </div>
  </div>
</template>

<script>

import { myStore, updateImg } from "@/api/store";
import { mapGetters } from "vuex";
export default {
  name: 'becomeVip',
  data () {
    return {
        value:'',
        active: 0 ,
        myStore:'',
    }
  },
  computed: {
    ...mapGetters(['user'])
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
       if(this.user){
          myStore().then(res => {
            this.myStore = res.data.result;
          });
       }

    },
    become(){
      if(this.myStore){
          this.$toast('The data has been submitted, please do not submit again');
      }else{
          this.$router.push('becomeSeller');
      }

    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.$router.back(-1)
    },
    confirm(){
        this.$dialog
            .confirm({
          message: "Confirm to dial our service" +'<br>'
          +"NO.+971 988888888",
          confirmButtonText: "Confirm",
          cancelButtonText: "Cancel"
          }).then(() => {

              window.location.href = 'wtai://wp//mc;971-988888888'
          });

    }


  }
}
</script>

<style lang="scss" scoped>
    .bkground{
        height: 250px;
        background-color: #efe2ca;
        width: 100%;
        .head{
          background-color: #efe2ca;
          .cart{
            font-size: 22px;
          }
        }
    }
    .center{
      position: absolute;
      top: 60px;
      img{
        width: 110%;
        height: 545px;
        position: absolute;
        left: -5%;
      }
        .describe{
            padding: 0 6%;
            padding-top: 155px;
            position: relative;
            z-index: 111;
            p{
              padding-top: 10px;
              font-size: 14px;
              color: #111111;
            }
            span{
              padding: 5px 4% 0;
              font-size: 13px;
              color: #666666;
              display: block;
              i{
                color: #2b87d5;
              }
            }
            .become{
              padding-top: 10px;
            }
        }
    }
</style>



